* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Montserrat', sans-serif;
    ::selection {
        color: #3215db;
        background-color: #ebf04e;
    }
}

.container {
    max-width: 1220px;
    margin: 0 auto;
    padding: 0 15px;
}
.container-big {
    margin: 0 auto;
    padding: 0 10px;

}

.column-block {
    display: flex;
    align-items: stretch;
    .left-block,
    .right-block {
        width: 50%;
        display: flex;
    }
    
    .left-block {
        justify-content: end;
    }
}


.small-container {
    width: 600px;
}

.btn {
    font-weight: 500;
    font-size: 18px;
    line-height: 22px;
    text-transform: uppercase;
    background: #2FA84F;
    border: none;
    border-radius: 40px;
    color: #FFFFFF;
    cursor: pointer;
    &:hover {
        box-shadow: rgba(0, 0, 0, 0.17) 0px -23px 25px 0px inset, rgba(0, 0, 0, 0.15) 0px -36px 30px 0px inset, rgba(0, 0, 0, 0.1) 0px -79px 40px 0px inset, rgba(0, 0, 0, 0.06) 0px 2px 1px, rgba(0, 0, 0, 0.09) 0px 4px 2px, rgba(0, 0, 0, 0.09) 0px 8px 4px, rgba(0, 0, 0, 0.09) 0px 16px 8px, rgba(0, 0, 0, 0.09) 0px 32px 16px;    
    }
    &:active {
        box-shadow: inset -2px 1px 15px 16px rgba(0, 0, 0, 0.2), inset 4px 1px 20px 16px rgba(0, 0, 0, 0.2);
    }    
    &.btn-primary {
        padding: 19px 108px;
    }
}